import * as React from 'react';
import Card from '@mui/material/Card';
import CardContent from '@mui/material/CardContent';
import CardMedia from '@mui/material/CardMedia';
import Typography from '@mui/material/Typography';
import { CardActionArea } from '@mui/material';
import '@fontsource/roboto/500.css';
import './ProductCard.css';
import FavoriteButton from './FavoriteButton/FavoriteButton';
import ChipBadge from './Chip/ChipBadge';
import {Box} from '@mui/material';
import LocationOnIcon from '@mui/icons-material/LocationOn';
import Avatar from '@mui/material/Avatar';
import AvatarImage from '../../../../images/avatar.png'

export default function ProductCard(props) {
  const ProductImage = props.image.type;
  return (
    <Card className="card-root" sx={{ maxWidth: "30%", maxHeight: 476 }}>
      
      <CardActionArea className="actionArea">
        <FavoriteButton />
        <CardMedia
          component="img"
          src={ProductImage}
          alt={props.title}
          width= "207px"
          height="263px"
          className="productImage"
        />
        <ChipBadge />
        <CardContent className="cardContent">
        <Typography gutterBottom className="brand" variant="p" component="div">
          {props.brand}
          </Typography>
          <Typography gutterBottom className="title" variant="subtitle2" component="div">
            {props.title}
          </Typography>
          <Box height="24px"></Box>
          <Box className="cardFooter-container">
            <Box className="price-container">
              <Box className='oldPrice'>
                <Typography variant="caption" display="block" gutterBottom>
                  {props.oldPrice}
                </Typography>
              </Box>
              <Box className="newPrice">
                <Typography variant="caption" display="block" gutterBottom>
                  {props.newPrice}
                </Typography>
              </Box>
            </Box>
            <Box className="sellerInfo-container">
              <Box className="sellerGeo">
              <LocationOnIcon sx={{width: "10px", height: "10px"}}/>
                <Typography gutterBottom>
                 {props.sellerGeo}
                </Typography>
              </Box>
              <Box className="sellerName">
              <Avatar alt="avatar" src={props.avatar.type} className="avatar"/>
                <Typography gutterBottom>
                  {props.sellerName}
                </Typography>
              </Box>
            </Box>
            
          </Box>
          
        </CardContent>
      </CardActionArea>
    </Card>
  );
}
