import { Box } from "@mui/system"
import styled from "styled-components"
import { Link, Typography } from "@mui/material"
import GooglePlay from '../../images/googleplay.png';
import {Divider} from "@mui/material";


export default function Footer() {
    const itemList1 = [
        {
            id: 1,
            text: "Довідка для покупців",
        },
        {
            id: 2,
            text: "Отримати підтримку",
        },
        {
            id: 3,
            text: "Як купувати",
        },
        {
            id: 4,
            text: "Як залишити корисний відгук",
        },
        {
            id: 5,
            text: "Програма Захисту Покупців",
        },
        {
            id: 6,
            text: "Рекомендації з безпечних покупок",
        },
    ]
    const itemList2 = [
        {
            id: 1,
            text: "Довідка для продавців",
        },
        {
            id: 2,
            text: "Як почати продавати",
        },
        {
            id: 3,
            text: "Тарифи",
        },
        {
            id: 4,
            text: "Угода користувача",
        },
        {
            id: 5,
            text: "Політика конфіденційності",
        },
        {
            id: 6,
            text: "Політика конфіденційності",
        },
        {
            id: 7,
            text: "Бонусна програма",
        },
    ]
    const itemList3 = [
        {
            id: 1,
            text: "Про нас",
        },
        {
            id: 2,
            text: "Довідка та FAQ",
        },
        {
            id: 3,
            text: "Захист легальності контенту",
        },
        {
            id: 4,
            text: "Адміністрація",
        },
        {
            id: 5,
            text: "Вакансії",
        },

    ]
    const itemList4 = [
        {
            id: 1,
            text: "Мобільний додаток",
        }
    ]
    return(
        <>
            <Container>
                <Box className="footer-container">
                    <Box className="footer-root">
                        <Box className="footerLinks-container">
                            <Box className="block1">
                                {itemList1.map(item => {
                                    return(
                                        <Link gutterBottom key={item.id} href="#" underline="none">
                                            {item.text}
                                        </Link>
                                    )   
                                })}
                            </Box>
                            <Box className="block2">
                                {itemList2.map(item => {
                                    return(
                                        <Link gutterBottom key={item.id} href="#" underline="none">
                                            {item.text}
                                        </Link>
                                    )   
                                })}
                            </Box>
                            <Box className="block3">
                                {itemList3.map(item => {
                                    return(
                                        <Link gutterBottom key={item.id} href="#" underline="none">
                                            {item.text}
                                        </Link>
                                    )   
                                })}
                            </Box>
                            <Box className="block4">
                            {itemList4.map(item => {
                                    return(
                                        <Link gutterBottom key={item.id} href="#" underline="none">
                                            {item.text}
                                        </Link>
                                    )   
                            })}
                            <Link gutterBottom href="#" underline="none">
                                <img src={GooglePlay}/>
                            </Link>
                                
                            </Box>
                        </Box>
                    </Box>
                    <Divider />
                    <Typography className="rights">
                    © 2022 Monvalise
                    </Typography>
                </Box>
            </Container>
        </>
    )
}

const Container = styled.div`
height: 457px;
width: 100%;
background: #EFF1F1;
.footer-container {
    width: 90%;
    margin: 0 auto;
    padding-top:72px;
    margin-bottom: 24px;
}
.footer-root {
    margin-bottom: 72px;
}
.footerLinks-container {
    display:flex;
    justify-content: space-between;
}
.footerLinks-container > .MuiBox-root {
    max-width: 312px;
    display: flex;
    flex-direction: column;
    
}
.footerLinks-container > .MuiBox-root > a {
    font-size: 14px;
    color: #2D3132;
    font-family: 'Ubuntu';
    font-weight: 500;
    margin-bottom: 12px;
}
.rights {
    padding-top: 38px;
    font-size: 12px;
    font-weight: 400;
}
`