import Breadcrumb from '../Breadcrumb/Breadcrumb';
import Sidebar from './Sidebar/Sidebar';
import Products from './Products/Products';
import Box from '@mui/material/Box';
import InputLabel from '@mui/material/InputLabel';
import Typography from '@mui/material/Typography';
import Sort from './Sort/Sort';
import './Catalog.css';


export default function Catalog() {
    return(
        <>  
            <Box>
                <Breadcrumb />
                <Box className="titleSort-container">
                    <Box className="title-container">
                    <h2>Каталог</h2>
                    </Box>
                    <Box className="sort-container">
                        <Typography className="sort-title" variant="body2" gutterBottom>
                        <InputLabel sx={{fontSize: "12px",}} id="demo-simple-select-label">Сортувати за:</InputLabel>
                        </Typography>
                        <Sort/>
                    </Box>
                </Box>
                <Box className='sidebarProducts-container'>
                    <Sidebar/>
                    <Products />
                </Box>
            </Box>
        </>
    )
}