import * as React from 'react';
import PropTypes from 'prop-types';
import Tabs from '@mui/material/Tabs';
import Tab from '@mui/material/Tab';
import Typography from '@mui/material/Typography';
import Box from '@mui/material/Box';
import { Divider } from '@mui/material';
import './Tabs.css';
import Catalog from '../../Catalog/Catalog'

function TabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`simple-tabpanel-${index}`}
      aria-labelledby={`simple-tab-${index}`}
      {...other}
    >
      {value === index && (
        <Box sx={{ p: 3 }}>
          <Typography component={'span'}>{children}</Typography>
        </Box>
      )}
    </div>
  );
}

TabPanel.propTypes = {
  children: PropTypes.node,
  index: PropTypes.number.isRequired,
  value: PropTypes.number.isRequired,
};

function a11yProps(index) {
  return {
    id: `simple-tab-${index}`,
    'aria-controls': `simple-tabpanel-${index}`,
  };
}

export default function TabSimple() {
  const [value, setValue] = React.useState(0);

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  return (
    <>
    <Box sx={{ width: '100%' }}>
      <Box sx={{  }}>
        <Tabs value={value} onChange={handleChange} textColor="#444748" aria-label="basic tabs example">
          <Tab label="Головна" {...a11yProps(0)} />
          <Tab label="Каталог" {...a11yProps(1)} />
          <Tab label="Жінкам" {...a11yProps(2)} />
          <Tab label="Чоловікам" {...a11yProps(3)} />
          <Tab label="Дитячі речі" {...a11yProps(4)} />
          <Tab label="Аукціон" {...a11yProps(5)} />
          <Tab label="Бренди" {...a11yProps(6)} />
          <Tab label="Акції" {...a11yProps(7)} />
          <Tab label="Аутентифікація" {...a11yProps(8)} />
          <Tab label="Блог" {...a11yProps(9)} />
        </Tabs>
      </Box>
      <Divider variant='fullWidth' sx={DividerFullWidth}/>
      <TabPanel value={value} index={0}>
        Item 1
      </TabPanel>
      <TabPanel value={value} index={1}>
        <Catalog/>
      </TabPanel>
      <TabPanel value={value} index={2}>
        Item 3
      </TabPanel>
      <TabPanel value={value} index={3}>
        Item 4
      </TabPanel>
      <TabPanel value={value} index={4}>
        Item 5
      </TabPanel>
      <TabPanel value={value} index={5}>
        Item 6
      </TabPanel>
      <TabPanel value={value} index={6}>
        Item 7
      </TabPanel>
      <TabPanel value={value} index={7}>
        Item 8
      </TabPanel>
      <TabPanel value={value} index={8}>
        Item 9
      </TabPanel>
      <TabPanel value={value} index={9}>
        Item 10
      </TabPanel>
    </Box>
    
    </>
  );
}
const DividerFullWidth = {
    position: "absolute",
    width: "100%",
    left: "0px",
}
