import styled from 'styled-components'
import Navbar from './Navbar/Navbar'
import TabSimple from './Tabs/Tabs'
import { Divider } from '@mui/material';
export default function Header() {
    return (
        <>
            <Container>
                <Navbar />
                <TabSimple />
            </Container>
        </>
    )
}

const Container = styled.div`
    margin-bottom: 112px;
`