import * as React from 'react';
import { styled } from '@mui/material/styles';
import ArrowForwardIosSharpIcon from '@mui/icons-material/ArrowForwardIosSharp';
import MuiAccordion from '@mui/material/Accordion';
import MuiAccordionSummary from '@mui/material/AccordionSummary';
import MuiAccordionDetails from '@mui/material/AccordionDetails';
import Typography from '@mui/material/Typography';
import './Sidebar.css';
import PriceFilter from './Filters/PriceFilter/PriceFilter';
import SexFilter from './Filters/SexFilter/SexFilter';

const Accordion = styled((props) => (
  <MuiAccordion disableGutters elevation={0} square {...props} />
))(({ theme }) => ({
  border: `1px solid ${theme.palette.divider}`,
  '&:not(:last-child)': {
    borderBottom: 0,
  },
  '&:before': {
    display: 'none',
  },
}));

const AccordionSummary = styled((props) => (
  <MuiAccordionSummary
    expandIcon={<ArrowForwardIosSharpIcon sx={{ fontSize: '0.9rem' }} />}
    {...props}
  />
))(({ theme }) => ({
  backgroundColor:
    theme.palette.mode === 'dark'
      ? 'rgba(255, 255, 255, .05)'
      : 'rgba(0, 0, 0, .03)',
  flexDirection: 'row-reverse',
  '& .MuiAccordionSummary-expandIconWrapper.Mui-expanded': {
    transform: 'rotate(90deg)',
  },
  '& .MuiAccordionSummary-content': {
    marginLeft: theme.spacing(1),
  },
}));

const AccordionDetails = styled(MuiAccordionDetails)(({ theme }) => ({
  padding: theme.spacing(2),
  borderTop: '1px solid rgba(0, 0, 0, .125)',
}));

export default function Sidebar() {
  const [expanded, setExpanded] = React.useState('');

  const handleChange = (panel) => (event, newExpanded) => {
    setExpanded(newExpanded ? panel : false);
  };

  const itemList = [
    {
      id: 1, 
      label: "Категорії", 
      description: "Category", 
      panel: "panel1", 
      ariaControls: "panel1d-content", 
      cssId: "panel1d-header"
    },
    {
      id: 2,
      label: "Ціна",
      description: <PriceFilter />,
      panel: "panel2", 
      ariaControls: "panel2d-content", 
      cssId: "panel2d-header"
    },
    {
      id: 3,
      label: "Продавець",
      description: "Seller",
      panel: "panel3", 
      ariaControls: "panel3d-content", 
      cssId: "panel3d-header"
    },
    {
      id: 4,
      label: "Бренд",
      description: "Brand",
      panel: "panel4", 
      ariaControls: "panel4d-content", 
      cssId: "panel4d-header"
    },
    {
      id: 5,
      label: "Стать",
      description: <SexFilter/>,
      panel: "panel5", 
      ariaControls: "panel5d-content", 
      cssId: "panel5d-header"
    },
    {
      id: 6,
      label: "Розмір",
      description: "Size",
      panel: "panel6", 
      ariaControls: "panel6d-content", 
      cssId: "panel6d-header"
    },
    {
      id: 7,
      label: "Колір",
      description: "Color",
      panel: "panel7", 
      ariaControls: "panel7d-content", 
      cssId: "panel7d-header"
    },
    {
      id: 8,
      label: "Матеріал",
      description: "Fabric",
      panel: "panel8", 
      ariaControls: "panel8d-content", 
      cssId: "panel8d-header"
    },
    {
      id: 9,
      label: "Сезон",
      description: "Season",
      panel: "panel9", 
      ariaControls: "panel9d-content", 
      cssId: "panel9d-header"
    },
    {
      id: 10,
      label: "Стиль",
      description: "Style",
      panel: "panel10", 
      ariaControls: "panel10d-content", 
      cssId: "panel10d-header"
    },
  ]

  return (
    <div className='sidebar-container'>
      {itemList.map(item => (
        <Accordion sx={AccordionSx} onChange={handleChange(item.panel)}>
        <AccordionSummary aria-controls={item.ariaControls} id={item.cssId} variant="caption">
          <Typography component={'span'}>{item.label}</Typography>
        </AccordionSummary>
        <AccordionDetails>
          <Typography component={'span'} >
           {item.description}
          </Typography>
        </AccordionDetails>
      </Accordion>
      ))}
      
    </div>
  );
}

const AccordionSx = {
  width: "100%",
}