import * as React from 'react';
import Box from '@mui/material/Box';
import Slider from '@mui/material/Slider';
import './Slider.css';
import './Input.css';
import TextField from '@mui/material/TextField';
import { display } from '@mui/system';
import Alert from '@mui/material/Alert';

function valuetext(value) {
  return `${value}°C`;
}


const priceRange = [0, 1000];

export default function PriceFilter() {
  const [value, setValue] = React.useState(priceRange);

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };
 
  const handleChangeInput = (event, id) => {
    
    
    


    
    const enteredValue = event.target.value;
    const inputArray = value.map((item, i) => {
      if(id === "From" && i === 0) {
        item = enteredValue;
        return item;
      }
      else if (id === "From" && i === 1) {
        return item;
      }
      else if (id === "To" && i === 1) {
        item = enteredValue;
        return item;
      }
      else if (id === "To" && i === 0) {
        return item;
      }
    })
    setValue(inputArray);
  };

  const checkInputs = () => {
    var inputFrom = document.getElementsByName("From");
    var inputTo = document.getElementsByName("To");

    if(parseInt(inputFrom[0].value, 10) >= parseInt(inputTo[0].value, 10)) {
      console.log('notification-error');
    }
  }

  return (
      <Box sx={BoxSx}>
        <Slider
          getAriaLabel={() => 'Temperature range'}
          value={value}
          onChange={handleChange}
          valueLabelDisplay="auto"
          getAriaValueText={valuetext}
          sx = {SliderSx}
          min={0}
          max={2000}
          disableSwap
        />
        <Box sx={InputBoxSx}>
          <TextField 
            sx={FromInputSx}
            type="number" 
            id="standard-basic1" 
            label="Від" 
            variant="standard"
            name="From"
            onChange={(event) =>{ handleChangeInput(event, "From"); checkInputs()}} />
            {} 
          <TextField 
            sx={ToInputSx}
            type="number" 
            id="standard-basic2" 
            label="До"
            name="To" 
            variant="standard"
            onChange={(event) =>{ handleChangeInput(event, "To"); checkInputs()}} />
        </Box>
      </Box>
  );
}
const SliderSx = {
  width: "100%",
}
const BoxSx = { 
  width: "100%", 
}
const InputBoxSx = {
  display: "flex",
  justifyContent: "space-between",
}
const FromInputSx = {
  height: "45px",
  width: "120px",
}
const ToInputSx = {
  height: "45px",
  width: "120px",
}
